class MMD_SETTING {
  constructor() {
    this.init();
  }

  async init() {
    // ã‚·ãƒ¼ãƒ³ã®ä½œæˆ
    this.scene = new THREE.Scene();

    // å…‰ã®ä½œæˆ
    const ambient = new THREE.AmbientLight(0xeeeeee);
    this.scene.add(ambient);

    // ç”»é¢è¡¨ç¤ºã®è¨­å®š
    this.renderer = new THREE.WebGLRenderer({ alpha: true });
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(window.innerWidth, window.innerHeight);
    this.renderer.setClearColor(0xcccccc, 0);

    // documentã«MMDã‚’ã‚»ãƒƒãƒˆã™ã‚‹
    document.body.appendChild(this.renderer.domElement);

    // ã‚«ãƒ¡ãƒ©ã‚’ä½œæˆ (è¦–é‡Žè§’, ç”»é¢ã®ã‚¢ã‚¹ãƒšã‚¯ãƒˆæ¯”, ã‚«ãƒ¡ãƒ©ã«æ˜ ã‚‹æœ€çŸ­è·é›¢, ã‚«ãƒ¡ãƒ©ã«æ˜ ã‚‹æœ€é è·é›¢)
    this.camera = new THREE.PerspectiveCamera(50, window.innerWidth / window.innerHeight, 1, 1000);
    this.camera.position.set(0, 18, 50);

    // ã‚«ãƒ¡ãƒ©ã‚³ãƒ³ãƒˆãƒ­ãƒ¼ãƒ©ãƒ¼ã‚’ä½œæˆ
    const controls = new THREE.OrbitControls(this.camera);

    // æ»‘ã‚‰ã‹ã«ã‚«ãƒ¡ãƒ©ã‚³ãƒ³ãƒˆãƒ­ãƒ¼ãƒ©ãƒ¼ã‚’åˆ¶å¾¡ã™ã‚‹
    controls.enableDamping = true;
    controls.dampingFactor = 0.2;

    // æ¯Žãƒ•ãƒ¬ãƒ¼ãƒ æ™‚ã«å®Ÿè¡Œã•ã‚Œã‚‹ãƒ«ãƒ¼ãƒ—ã‚¤ãƒ™ãƒ³ãƒˆã§ã™
    const tick = () => {
      // ã‚«ãƒ¡ãƒ©ã‚³ãƒ³ãƒˆãƒ­ãƒ¼ãƒ©ãƒ¼ã‚’æ›´æ–°
      controls.update();

      // ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°
      this.renderer.render(this.scene, this.camera);

      requestAnimationFrame(tick);
    }

    tick();

    // ãƒ¢ãƒ‡ãƒ«ã¨ãƒ¢ãƒ¼ã‚·ãƒ§ãƒ³ã®èª­ã¿è¾¼ã¿æº–å‚™
    var modelFile = "./static/pmx/zenitsu/zenitsu_taifuku.pmx";

    const loader = new THREE.MMDLoader();

    const onProgress = (xhr) => {
      if (xhr.lengthComputable) {
        const percentComplete = xhr.loaded / xhr.total * 100;
        console.log(Math.round(percentComplete, 2) + '% downloaded');
      }
    }

    const onError = (xhr) => {
      console.log("ERROR");
    }

    const LoadPMX = () => {
      return new Promise(resolve => {
        loader.load(modelFile, (object) => {
          this.mesh = object;
          this.scene.add(this.mesh);

          resolve(true);
        }, onProgress, onError);
      });
    }

    await LoadPMX();

    const Render = () => {
      requestAnimationFrame(Render);
      this.renderer.clear();
      this.renderer.render(this.scene, this.camera);
    }

    Render();
  }
}

new MMD_SETTING();